<style scoped>
.chart-main {
    height: 100%;
    overflow: hidden;
    --border-stroke: #003153;
    --border-thick: 0.2rem;
    --border-radius: 0.8rem;
}

.chart-box {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    height: calc(100% - 0.01rem);
    float: top;
    border-radius: var(--border-radius);
}

@import "../../res/css/chartbox.css";
</style>
