<template>
    <div class="pf-param-line">
        <div class="block1">
            <span class="id-left-msg">
                ID:{{ tid }}
            </span>
        </div>
        <div class="block2">
            <span class="time-msg" :class="{ 'scroll': isOverflow }" ref="content">{{ sn }}</span>
        </div>
        <div class="block3">
            <span class="id-right-msg">{{ batchesinfo }}
            </span>
        </div>
        <span :class="lineClass"></span>
    </div>
</template>

<script>

export default {
    name: "NewSensor",
    data() {
        return {
            count: 1,
            isOverflow: false,
            
        };
    },

    props: {
        tid: {
            type: Number,
            default: 3213,
        },
        sn: {
            type: String,
            default: "1234567890",
        },
        batchesinfo: {
            type: String,
            default: "3/8",
        },
        isShowLine: {
            type: Boolean,
            default: true
        }
    },
    mounted() {
        const divWidth = this.$refs.content.parentNode.offsetWidth;
        const spanWidth = this.$refs.content.offsetWidth;
        spanWidth + divWidth * 0.35 > divWidth ? this.isOverflow = true : this.isOverflow = false
    },
    methods: {},
    computed: {
        limitColor() {
            if (this.sensorValue > this.max) {
                return "#f80606";
            } else if (this.sensorValue < this.min) {
                return "#2edbe7";
            } else {
                return "#121312";
            }
        },
        display_value() {
            var sensor = this.$store.state.sensors[this.sname];
            if (sensor == null) {
                sensor = {
                    gain: 1,
                    offset: 0,
                };
            }
            return (
                (parseFloat(this.sensorValue) * sensor.gain + sensor.offset).toFixed(this.fixed)
            );
        },
        lineClass() {
            return (this.isShowLine ? 'line' : "")
        }
    },    
};
</script>


<style scoped>
.pf-param-line {
    position: relative;
    height: 6.5625vh;
    width: 95vw;
    background-color: var(--Cee);
}

.block1 {
    position: absolute;
    top: 0;
    left: 42px;
    width: 10vw;
    height: 100%;
}

.id-left-msg {
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    position: absolute;
    top: calc(50% - 0.9rem);
    left: 0;
    font-family: var(--FSY);
    font-size: var(--font1_75);
}

.block2 {
    position: absolute;
    top: calc(50% - 1.8rem);
    left: 24vw;
    overflow: hidden;
    width: 54vw;
    text-align: center;
    height: 100%;
    font-size: 2.5rem;
}

.time-msg {
    display: inline-block;
    text-align: center;
    white-space: nowrap;
    font-size: 2.375rem;
    color: var(--C00);
    vertical-align: middle;
}

.block3 {
    position: absolute;
    right: 29px;
    top: 0;
    width: 10vw;
    height: 100%;
    font-size: 1.75rem;
}

.id-right-msg {
    position: absolute;
    display: inline-block;
    vertical-align: middle;
    top: calc(50% - 0.1rem);
    right: 0;
    transform: translateY(-50%);
}

.scroll {
    animation: scroll 15s linear infinite;
}

@keyframes scroll {
    0% {
        transform: translateX(0%);
    }

    100% {
        transform: translateX(calc(56vw - 100%));
    }
}

.line {
    position: absolute;
    bottom: 0;
    height: 1px;
    left: 2.375rem;
    right: 2rem;
    background-color: var(--CD4);
}

.fenzi {
    font-size: var(--font1_75);
    color: #000000;
}

.fenmu {
    color: #3B3B3B;
}
</style>
