<template>
    <div class="chart-main">
        <div id="txy" class="chart-box" />
        <div class="operate-chart-box" contenteditable="true" @blur="select_item_indx = -1">
            <div :class="{ 'select-chart': true, 'bgc-white': select_item_indx == 0 }"
                @click="select_item_indx = select_item_indx == 0 ? -1 : 0">
                <svg-loader class="chart-icon" size="2.5rem" icon="Chart" />
            </div>
            <div class="select-chart" @click="auto_range">
                <svg-loader class="chart-icon" size="2.5rem" :icon="isAutoFit ? 'ActiveAuto' : 'StaticAuto'" />
            </div>
            <div :class="{ 'select-chart': true, 'bgc-white': select_item_indx == 3 }" v-if="!isAutoFit" @click="fit">
                <svg-loader class="chart-icon" size="2.5rem" icon="AutoChart" />
            </div>
            <div class="chart-options" v-show="select_item_indx == 0" :contenteditable="false">
                <div :class="{ 'active-chart': $global_var.ui_config.chart_tab == 1 }" @click="switchChart(1)">
                    {{ $t("curve.torqueAngleCurve") }}
                </div>
                <div :class="{ 'active-chart': $global_var.ui_config.chart_tab == 2 }" @click="switchChart(2)">
                    {{ $t("curve.timeAngleTorqueCurve") }}
                </div>
                <div :class="{ 'active-chart': $global_var.ui_config.chart_tab == 3 }" @click="switchChart(3)">
                    {{ $t("curve.speedCurve") }}
                </div>
                <div :class="{ 'active-chart': $global_var.ui_config.chart_tab == 4 }" @click="switchChart(4)">
                    {{ $t("curve.jobChart") }}
                </div>
            </div>
        </div>
        <div class="x-label">time</div>
    </div>
</template>

<script>
import { CHART_TPT } from "@/components/charts/chart2Y";

import { chartProcessData } from "@/components/charts/chartProcessData";

function createChart() {
    CHART_TPT.createChart("txy");
}

export default {
    data() {
        return {
            popvisible: false,
            is_activated: false,
            showCursor: false,
            isAutoFit: true,
            select_item_indx: -1,
        };
    },
    props: {
        msg: String,
    },
    activated() {
        // this.is_activated = true;
        // if (
        //     chartProcessData.process_cache.pdata.times.length > 1 &&
        //     chartProcessData.chartName == "txy"
        // ) {
        //     this.addSeriesData(chartProcessData.process_cache.pdata);
        // }
        // chartProcessData.addEventHandler("txy", this.addSeriesData, this.clearSeriesData);
    },
    deactivated() {
        // this.is_activated = false;
        // chartProcessData.removeEventHandler("txy", this.addSeriesData, this.clearSeriesData);
        // this.clearSeriesData();
    },
    mounted() {
        createChart();
    },
    unmounted() {
        CHART_TPT.destory();
    },
    computed: {
        rangeColor() {
            return this.isAutoFit ? "lime" : "#f4f7f4";
        },
        activeChartType() {
            return this.$global_var.ui_config.chart_tab;
        },
        max_torque(){
            return this.$store.state.g_status.sensor_max * this.$store.state.sensors['torque'].gain + this.$store.state.sensors['torque'].offset;
        },
    },
    methods: {
        clearSeriesData() {
            if (CHART_TPT.is_init) CHART_TPT.clearSeriesData();
        },
        // 切换游标状态
        show_cursor_method() {
            this.select_item_indx = 1;
            this.showCursor = !this.showCursor;
            CHART_TPT.showCursor(this.showCursor);
        },
    
        fit() {
            CHART_TPT.fitWithEORange(0,this.max_torque);
        },
        auto_range() {
            this.select_item_indx = 2;
            this.isAutoFit = !this.isAutoFit;
            CHART_TPT.auto_fit= this.isAutoFit;
        },

        change_page(tab) {
            this.$emit("onShowChartChange", tab);
            chartProcessData.chartChanged(tab);
        },

        // 切换曲线
        switchChart(i) {
            this.select_item_indx = -1;
            this.popvisible1 = false;
            this.$global_var.ui_config.chart_tab = i;
            this.change_page(this.$global_var.ui_config.chart_tab);
        },
    },
};
</script>

<style scoped>
.chart-main {
    height: 100%;
    overflow: hidden;
    --border-stroke: #003153;
    --border-thick: 0.2rem;
    --border-radius: 0.8rem;
}

.chart-box {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    height: calc(100% - 0.01rem);
    float: top;
    border-radius: let(--border-radius);
}

@import "../../res/css/chartbox.css";
</style>
