<template>
  <div class="doughnut">
    <div>
      <span class="pset-id" :class="active == 1 ? 'active-span' : ''">{{ pset }}</span>
      <div class="fractions">{{ this.status.length }}/{{ total }}</div>
      <div class="percent">{{ percent }}</div>
      <div class="solid-line"></div>
      <div class="dotted-line"></div>
    </div>
    <!-- 甜甜圈 -->
    <div :id="prefix + '-' + pset" style="width: 13.25rem; height: 13.25rem;"></div>
  </div>
</template>

<script>
import { PieSegment } from "scichart/Charting/Visuals/SciChartPieSurface/PieSegment/PieSegment";
import {
  SciChartPieSurface,
  ESizingMode,
  EPieType
} from "scichart/Charting/Visuals/SciChartPieSurface/SciChartPieSurface";
import { EColor } from "scichart/types/Color";
import { SciChartJSLightTheme } from "scichart/Charting/Themes/SciChartJSLightTheme";

export default {
  name: 'pieChartTwo',
  data() {
    return {
      chart: undefined,
      domId: "",
      color: "#EEEEEE",
      radiusSize: [1],
      pieSegment: {},
    }
  },
  props: {
    pset: {
      type: String,
      default: "001",
    },
    status: {
      type: Array,
      default: function () {
        return []
      }
    },
    total: {
      type: Number,
      default: 7,
    },
    prefix: {
      type: String,
      default: "A"
    },
    active: {
      type: Number,
      default: 1,
    },
  },
  watch: {
    status: {
      handler(newValue, oldValue) {
        if (oldValue !== undefined) {
          this.updatePieSegments();
        }
      },
      immediate: true
    },
  },
  activated() {
    this.domId = this.prefix + "-" + this.pset
    this.initSciChart();
  },
  computed: {
    okCount() {
      return this.status.filter(item => item === 1).length;
    },
    percent() {
      return Math.round((this.okCount / this.total) * 100) + "%";
    },
  },
  methods: {
    async initSciChart() {
      let theme = new SciChartJSLightTheme();
      theme.sciChartBackground = "transparent";
      const sciChartPieSurface = await SciChartPieSurface.create(this.domId, {
        theme,
        pieType: EPieType.Donut,
        holeRadius: 0.8,
        holeRadiusSizingMode: ESizingMode.Relative,
        seriesSpacing: 5,
      });
      // sciChartPieSurface.applyTheme(new SciChartJSLightTheme());
      //提示标签距离圆心位置
      sciChartPieSurface.labelRadiusAdjustment = 0;
      // 标签默认样式
      sciChartPieSurface.labelStyle = { fontSize: 0, color: EColor.White };
      // 不显示图例
      sciChartPieSurface.legend.showLegend = false;
      //禁止点击时的动画效果
      sciChartPieSurface.animate = false;

      const toPieSegment = (value, radiusAdjustment, color) => {
        return new PieSegment({
          value,
          radiusAdjustment,
          //不显示图例
          showLabel: value > 2,
          color: color
        });
      };

      let pieSegments = [new PieSegment({
          value: 0.001,
          radiusAdjustment: this.radiusSize[0],
          showLabel: false,
          color: '#ffffff',
        })];
      for (let i = 0; i < this.total; i++) {
        let pieSegment = toPieSegment(1, this.radiusSize[0], "#EEEEEE");
        pieSegments.push(pieSegment);
      }
      for (let i = 0; i < this.status.length; i++) {
        if (this.status[i] == 0) {
          pieSegments[i].color = '#AC0000'
        } else {
          pieSegments[i].color = '#00C155'
        }
      }
      sciChartPieSurface.pieSegments.add(...pieSegments);
      this.chart = sciChartPieSurface;
    },
    updatePieSegments() {
      // const pieSegments = this.chart.pieSegments.asArray();
      const newPieSegments = [new PieSegment({
          value: 0.001,
          radiusAdjustment: this.radiusSize[0],
          showLabel: false,
          color:'#ffffff',
        })];

      for (let index = 0; index < this.total; index++) {
        const newSegment = new PieSegment({
          value: 1,
          radiusAdjustment: this.radiusSize[0],
          showLabel: false,
          color: index >= this.status.length ? '#EEEEEE' : (this.status[index] == 0 ? '#AC0000' : '#00C155'),
        });
        // 将新的 PieSegment 添加到新的数组中
        newPieSegments.push(newSegment);

      }
      if (this.chart) {
        this.chart.pieSegments.clear();

        // 添加新的 PieSegment 数组
        this.chart.pieSegments.add(...newPieSegments);

        this.chart.invalidateElement();
      }
      // pieSegments.forEach((segment, index) => {
      //   const newSegment = new PieSegment({
      //     value: segment.value,
      //     radiusAdjustment: segment.radiusAdjustment,
      //     showLabel: segment.showLabel,
      //     color: index >= this.status.length ? '#EEEEEE' : (this.status[index] == 0 ? '#AC0000' : '#00C155'),
      //   });
      //   // console.log("segment",segment)
      //   // console.log("index",index)
      //   // 将新的 PieSegment 添加到新的数组中
      //   newPieSegments.push(newSegment);

      // });
      // 移除原始的 PieSegment


    },
  }
};
</script>

<style scoped>
.doughnut {
  position: relative;
  width: 200px;
  height: 200px;
  margin-left: 0.2rem;
}

#scichart-donut {
  width: 200px;
  height: 300px;
  z-index: -1;
}

.span-text {
  position: absolute;
  top: 0;
  right: 15rem;
}

.pset-id,
.fractions,
.percent {
  position: absolute;
  top: 5.5rem;
  left: 3.2rem;
  font-size: 26px;
}

.fractions {
  top: 4.3rem;
  left: 6.8rem;
  font-size: 22px;
}

.percent {
  top: 7.1rem;
  left: 6.9rem;
  font-size: 22px;
}

.solid-line {
  position: absolute;
  top: 3rem;
  left: 6.6rem;
  width: 1rem;
  height: 7.3rem;
  border-left: 1px solid #ccc;
}

.dotted-line {
  position: absolute;
  top: 6.6rem;
  left: 6.9rem;
  width: 4.2rem;
  height: 1rem;
  border-top: 1px dashed #ccc;
}

.active-span {
  color: #00C155;
}
</style>
