import {
    processWebsocket
} from "@/api/websocketApi";
import { vuexStore as STORES } from "@/store";
import { CHART_TPT } from "./chart2Y";
import { CHART_XY, CHART_TSPD } from "./chart";
const chartNames = ['null', 'xy', 'txy', 'ts']

class ChartProcessData {
    constructor() {
        this.ws = null;
        this.IsRunning = false;
        // this.seriesDataHandlers = {};
       this.sensor_max=0;
        this.process_cache = {
            len: 0,
            pdata: {
                times: [],
                pos: [],
                sensor_val: [],
                speeds: [],
            }
        };
        this.resetting = false;
    }

    close() {
        if (this.ws)
            this.ws.close();
        this.ws = null;
        this.seriesDataHandlers = {};
        this.clearDataHandlers = null;
        this.IsRunning = false;
    }
    // addEventHandler(event, addhandler, clearhandler) {
    //     this.seriesDataHandlers[event] = this.seriesDataHandlers[event] || new Set();
    //     this.seriesDataHandlers[event].add(addhandler);
    //     this.clearDataHandlers[event] = this.clearDataHandlers[event] || new Set();
    //     this.clearDataHandlers[event].add(clearhandler);
    // }

    // removeEventHandler(event, handler, clearhandler) {
    //     this.seriesDataHandlers[event].delete(handler);
    //     this.clearDataHandlers[event].delete(clearhandler);
    // }

    createWs() {
        processWebsocket.msghandler = (msg) => {
            try {
                this.resolvePdata(msg);
                if( this.sensor_max==0)
                {
                    this.sensor_max= STORES.state.g_status.sensor_max * STORES.state.sensors['torque'].gain + STORES.state.sensors['torque'].offset;
                }
            } catch (error) {
                console.log(error);
            }
        };
        processWebsocket.openhandler = () => {
           
            this.IsRunning = true;
        };
        processWebsocket.closehandler = () => {
            this.IsRunning = false;
            this.sensor_max=0;
        };
        processWebsocket.open();
    }

    clearCache() {
        this.process_cache.len = 0;
        this.process_cache.pdata.times.length = 0;
        this.process_cache.pdata.sensor_val.length = 0;
        this.process_cache.pdata.pos.length = 0;
        this.process_cache.pdata.speeds.length = 0;
    }

    resolvePdata(msg) {
        let tmp_pdata = JSON.parse(msg);
        if (tmp_pdata.Result == "DATA_NEW") {
            this.clearCache();
            this.clearSeriesData()
        }
        else {
            tmp_pdata.sensor_val = tmp_pdata.sensor_val.map((x) => x * STORES.state.sensors['torque'].gain + STORES.state.sensors['torque'].offset);
            tmp_pdata.pos = tmp_pdata.pos.map((x) => x * STORES.state.sensors['angle'].gain + STORES.state.sensors['angle'].offset);
            tmp_pdata.speeds = tmp_pdata.speeds.map((x) => x * STORES.state.sensors['angle_speed'].gain + STORES.state.sensors['angle_speed'].offset);
            let len = this.process_cache.pdata.times.push(...tmp_pdata.times);
            this.process_cache.pdata.sensor_val.push(...tmp_pdata.sensor_val);
            this.process_cache.pdata.pos.push(...tmp_pdata.pos);
            this.process_cache.pdata.speeds.push(...tmp_pdata.speeds);
            if (tmp_pdata.pos.length > 1 && tmp_pdata.pos.length == tmp_pdata.speeds.length) {
                this.raiseSeriesData(tmp_pdata);
            }
            this.process_cache.len = len;
        }
        tmp_pdata = null;
    }

    raiseSeriesData(pdata) {
        switch (this.chartName) {
            case "xy":
                CHART_XY.addSeriesData(pdata.pos, pdata.sensor_val);
                if (CHART_XY.auto_fit) CHART_XY.fitWithEORange(0, this.sensor_max);
                break;
            case "txy":
                CHART_TPT.addSeriesData(pdata.times, pdata.pos, pdata.sensor_val);
                if (CHART_TPT.auto_fit) CHART_TPT.fitWithEORange(0, this.sensor_max)
                break;
            case "ts":
                CHART_TSPD.addSeriesData(pdata.times, pdata.speeds);
                if (CHART_TSPD.auto_fit) CHART_TSPD.fit()
                break;
            default:
                break;
        }
    }
    clearSeriesData() {
        switch (this.chartName) {
            case "xy":
                    CHART_XY.clearSeriesData();
                break;
            case "txy":
                    CHART_TPT.clearSeriesData();
                break;
            case "ts":
                    CHART_TSPD.clearSeriesData();
                break;
            default:
                break;
        }
    }


    chartChanged(tab) {
        // this.clearSeriesData();
        this.chartName = chartNames[tab];
        this.clearSeriesData();
        if (this.process_cache.len > 1)
            this.raiseSeriesData(this.process_cache.pdata)
    }

}

export const chartProcessData = new ChartProcessData();

export default { chartProcessData }