<template>
    <div class="monitor-box">
        <Right class="alarm-box" v-if="stateBgc == 2"></Right>
        <Runing class="alarm-box" v-if="stateBgc == 3"></Runing>
        <Ready class="alarm-box" v-if="stateBgc == 4"></Ready>
        <DevcieAlarm class="alarm-box" v-if="stateBgc == 5"></DevcieAlarm>
        <div class="monitor-values">
            <IDLine v-show="stateBgc != 3" :tid="this.$store.state.g_status.tid" :is="componentName" :key="componentKey"
                :sn="`${this.$store.state.g_status.ret_sn ?? '--'}`"
                :batchesinfo="`${this.$store.state.g_status.ret_cnt}/${this.$store.state.g_status.ret_batches}`"></IDLine>
            <NewSensor v-show="stateBgc != 3" :sensorName="$t('monitor.target_angle')"
                :min="this.$store.state.g_status.ret_pos_min" :max="this.$store.state.g_status.ret_pos_max" sname="angle"
                :sensorValue="this.$store.state.g_status.ret_pos" :sensorUnit="updateUnit('angle')">
            </NewSensor>
            <NewSensor v-show="stateBgc != 3" :sensorName="$t('monitor.target_torque')"
                :min="this.$store.state.g_status.ret_sensor_min" :fixed="3" :max="this.$store.state.g_status.ret_sensor_max"
                sname="torque" :sensorValue="this.$store.state.g_status.ret_sensor" :sensorUnit="updateUnit('torque')"
                :isShowLine="false"></NewSensor>
            <NewSensor v-show="stateBgc == 3" :sensorName="$t('monitor.angle')" sname="angle" min="--" max="--"
                :sensorValue="this.$store.state.g_status.pos" :sensorUnit="updateUnit('angle')">
            </NewSensor>
            <NewSensor v-show="stateBgc == 3" :sensorName="$t('monitor.torque')" sname="torque"
                :sensorValue="this.$store.state.g_status.sensor" :fixed="3" :sensorUnit="updateUnit('torque')" min="--"
                max="--"></NewSensor>
            <NewSensor v-show="stateBgc == 3" :sensorName="$t('monitor.speed')" sname="angle_speed" :isShowLine="false"
                :displayLimit="false" :sensorValue="this.$store.state.g_status.speed"
                :sensorUnit="updateUnit('angle_speed')"></NewSensor>
        </div>
        <div :class="{ 'chart-long-box': !showFooter, 'chart-box': showFooter }" class="item1">
            <chartPosSensor @onShowChartChange="onShowChartChange" />
        </div>
        <div class="chart-box item2">
            <chartRealPosSensor @onShowChartChange="onShowChartChange" />
        </div>
        <div class="chart-box item3">
            <chartRealSpeed @onShowChartChange="onShowChartChange" />
        </div>
        <div class="chart-box item4">
            <!-- <chartPie @onShowChartChange="onShowChartChange" /> -->
            <chartDot @onShowChartChange="onShowChartChange" :is="componentJobName" :key="componentJobKey" />
        </div>
    </div>
</template>

<script>

import {
    setUiConfig
} from "@/store/localStorage";
// 曲线图
import chartRealPosSensor from "@/components/charts/chartRealPosSensor.vue";
import chartPosSensor from "@/components/charts/chartPosSensor.vue";
import chartRealSpeed from "@/components/charts/chartRealSpeed.vue";
// import chartPie from "@/components/charts/chartPie.vue";
import chartDot from "@/components/charts/chartDot.vue";

import { psetProps } from "../api/profile/profile";
// 合格组件
import Right from "../components/alarmerror/right/Right.vue";
// 运行中组件
import Runing from "@/components/alarmerror/running/Runing.vue";
import NewSensor from "@/components/controls/NewSensor.vue";

import DevcieAlarm from "../components/alarmerror/DeviceAlarm/DevcieAlarm.vue";

import {
    chartProcessData
} from "@/components/charts/chartProcessData";
import IDLine from "@/components/controls/IDLine.vue";
import Ready from "../components/alarmerror/ready/Ready.vue";
import { CHART_XY } from "@/components/charts/chart"

export default {
    components: {
        chartRealPosSensor,
        chartPosSensor,
        chartRealSpeed,
        // chartPie,
        chartDot,
        Right,
        Runing,
        NewSensor,
        IDLine,
        Ready,
        DevcieAlarm,
    },
    data() {
        return {
            pdata_cnt: 0,
            select_chart_index: 1,
            eoRange: {
                pos: {
                    Min: 0,
                    Max: 5
                },
                sensor: {
                    Min: 2,
                    Max: 12
                }
            },
            eoRangeInit: false,
            componentName: "ChildComponent", // 要渲染的初始组件名
            componentKey: 1, // 初始的组件 key
            componentJobName: "ChildJobComponent", // 要渲染的初始组件名
            componentJobKey: 1, // 初始的组件 key
            showFooter: process.env.VUE_APP_SPECIAL !== 'SPECIAL' || this.$store.state.f_status.user === "lx",
            // showFooter: true,
        };
    },
    methods: {
        onShowChartChange(tab) {
            this.select_chart_index = tab;
            setUiConfig(this.$global_var, {
                chart_tab: tab,
            });
        },
        updateUnit(uname) {
            return this.$store.state.sensors[uname].name;
        },
        getResult() {
            this.res.torque = this.$store.state.g_status.ret_sensor.toFixed(3);
            this.res.angle = this.$store.state.g_status.ret_pos.toFixed(3);
        },
        sensorUnit(uname) {
            return this.$store.state.sensors[uname].name;
        },
        changeVal(key) {
            var val = this.$store.state.g_status[key];
            this.$store.state.g_status[key] = val == 1 ? 0 : 1;
        },
        setEoRange(offset) {
            psetProps(this.$store.state.g_status.pset).then((x) => {
                if (x.data && x.data.pos && x.data.sensor) {
                    x.data.pos.Min += offset;
                    x.data.pos.Max += offset;
                    this.eoRange = x.data
                    CHART_XY.setEoRange(x.data.pos.Min * this.$store.state.sensors['angle'].gain + this.$store.state.sensors['angle'].offset,
                        x.data.pos.Max * this.$store.state.sensors['angle'].gain + this.$store.state.sensors['angle'].offset,
                        x.data.sensor.Min * this.$store.state.sensors['torque'].gain + this.$store.state.sensors['torque'].offset,
                        x.data.sensor.Max * this.$store.state.sensors['torque'].gain + this.$store.state.sensors['torque'].offset
                    )
                    if (CHART_XY.is_init && CHART_XY.auto_fit)
                        CHART_XY.fitWithEO();
                }
            });
        },
    },
    computed: {
        anglelangProp() {
            return this.$i18n.t("param.angle");
        },
        torquelangProp() {
            return this.$i18n.t("param.torque");
        },
        speedlangProp() {
            return this.$i18n.t("param.speed");
        },
        stateBgc() {
            var state = 4;
            // 设备报错
            if (this.$store.state.g_status.alarmed) {
                state = 5;
            }
            // 结果状态
            else if (this.$store.state.g_status.rok && !this.$store.state.g_status.busy) {
                state = 2;
            }
            // 运行中
            else if (this.$store.state.g_status.busy) {
                state = 3;
            }
            // 准备状态
            else {
                state = 4;
            }
            return state;
        },
        runOk() {
            return !this.$store.state.g_status["busy"] && this.$store.state.g_status["rok"];
        },
        tab1_opacity() {
            return this.select_chart_index == 1 ? "visible" : "hidden";
        },
        tab2_opacity() {
            return this.select_chart_index == 2 ? "visible" : "hidden";
        },
        tab3_opacity() {
            return this.select_chart_index == 3 ? "visible" : "hidden";
        },
        tab4_opacity() {
            return this.select_chart_index == 4 ? "visible" : "hidden";
        },

    },
    mounted() {
        this.select_chart_index = this.$global_var.ui_config.chart_tab;
        chartProcessData.chartChanged(this.$global_var.ui_config.chart_tab);

        this.$watch(
            () => this.$store.state.g_status.G_Inflexion_Angle,
            (newVal) => {
                CHART_XY.setRectEO(newVal * this.$store.state.sensors['angle'].gain + this.$store.state.sensors['angle'].offset, this.$store.state.g_status.G_Inflexion_Force * this.$store.state.sensors['torque'].gain + this.$store.state.sensors['torque'].offset);
            }
        );
        // this.$watch(
        //     () => this.$store.state.g_status.pset,
        //     () => {
        //         this.setEoRange(0);
        //     });
        //     this.$watch(
        //         () => this.$store.state.g_status.busy,
        //         (new1, old1) => {
        //             if (old1) {
        //                 this.setEoRange(0);
        //             }
        //         });
        //     this.$watch(
        //         () => this.$store.state.g_status.eopos,
        //         (offset) => {
        //             this.setEoRange(offset);
        //         }
        //         , { immediate: true });
    },
    created() {
        this.$watch(
            () => this.$store.state.g_status.rok,
            (newVal) => {
                this.setEoRange(this.$store.state.g_status.eopos);
                CHART_XY.setEoResult(newVal, this.$store.state.g_status.isok);
            },
        );

        this.$watch(
            () => this.$store.state.g_status.segment.length,

            (newVal) => {
                // console.log(this.$store.state.g_status.segment)
                if (newVal === 0) {
                    CHART_XY.clearStepsRectEO()
                } else {
                    CHART_XY.addStepsRectEO(this.$store.state.g_status.segment.slice(0, newVal - 1).map((se) => {
                        return {
                            angle: se.angle * this.$store.state.sensors['angle'].gain + this.$store.state.sensors['angle'].offset,
                            torque: se.torque * this.$store.state.sensors['torque'].gain + this.$store.state.sensors['torque'].offset,
                        }

                    }))
                }

            },
        );

        setTimeout(() => {
            chartProcessData.createWs();
        }, 1000)
    },
    watch: {
        "$store.state.g_status.ret_sn": {
            handler() {
                this.componentName = "ChildComponent"; // 更新为需要重新创建的组件名
                this.componentKey++; // 更新组件 key，用于销毁当前组件并创建新的组件
            },
            immediate: true

        },
        "$store.state.g_status.job": {
            handler() {
                this.componentJobName = "ChildJobComponent";
                this.componentJobKey++;
            },
            immediate: true

        }
    }
}

</script>

