<template>
    <div class="job-options-box">
        <div class="operate-chart-box1" contenteditable="true" @blur="select_item_indx = -1">
            <div :class="{ 'select-chart': true, 'bgc-white': select_item_indx == 0 }"
                @click="select_item_indx = select_item_indx == 0 ? -1 : 0">
                <svg-loader class="chart-icon" size="2.5rem" icon="Chart" />
            </div>
            <!-- <div class="select-chart" @click="auto_range">
                <svg-loader class="chart-icon" size="2.5rem" :icon="isAutoFit ? 'ActiveAuto' : 'StaticAuto'" />
            </div>
            <div :class="{ 'select-chart': true, 'bgc-white': select_item_indx == 3 }" v-if="!isAutoFit" @click="fit">
                <svg-loader class="chart-icon" size="2.5rem" icon="AutoChart" />
            </div> -->
            <div class="chart-box1" v-show="select_item_indx == 0" :contenteditable="false">
                <div :class="{ 'active-chart': $global_var.ui_config.chart_tab == 1 }" @click="switchChart(1)">
                    {{ $t("curve.torqueAngleCurve") }}
                </div>
                <div :class="{ 'active-chart': $global_var.ui_config.chart_tab == 2 }" @click="switchChart(2)">
                    {{ $t("curve.timeAngleTorqueCurve") }}
                </div>
                <div :class="{ 'active-chart': $global_var.ui_config.chart_tab == 3 }" @click="switchChart(3)">
                    {{ $t("curve.speedCurve") }}
                </div>
                <div :class="{ 'active-chart': $global_var.ui_config.chart_tab == 4 }" @click="switchChart(4)">
                    {{ $t("curve.jobChart") }}
                </div>
            </div>
        </div>
        <div v-for="(items, i) in jobChartData()" :key="i" class="job-options-box_1">
            <!-- <div v-for="(items, i) in this.jobChartItems" :key="i" class="job-options-box_1"> -->
            <div v-if="i" class="job-options-box_line1">
            </div>
            <div class="job-options-box_f">&</div>
            <div v-for="(pset, j) in items" :key="j" class="job-options-box_2">
                <div class="job-options-box_3" :class="pset.Active == 1 ? 'active-box' : ''">
                    <div class="job-options-box_line2"></div>
                    <!-- <svg-loader size="3rem" class="file-static-icon" :icon="pset.Active == 1 ? 'ActivePf' : 'StaticPf'">
                    </svg-loader>
                    <span class="pf-static-num" :class="pset.Active==1?'active-span':''">{{ pset.Pset }}</span>-->
                    <doughnutPie class="doughtPie-box" :prefix="pset.Prefix" :pset="pset.Pset" :total="pset.Total"
                        :active="pset.Active" :status="pset.Status" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import doughnutPie from "@/components/charts/doughnutPie.vue"
import { chartProcessData } from "@/components/charts/chartProcessData";
export default {
    components: {
        doughnutPie
    },
    props: {},
    data() {
        return {
            jobChartItems: [
                [
                    {
                        "Pset": "001", //pest号
                        "Total": 7, //一共需要打的个数
                        "Status": [], //已打的状态 1为成功，0为失败
                        "Prefix": 'A',//第一行赋值A，第二行B
                        "Active": 1
                    }
                ]
            ],
            select_item_indx: -1,
        }
    },
    computed: {},
    watch: {},
    created() { },
    mounted() { },
    methods: {
        change_page(tab) {
            this.$emit("onShowChartChange", tab);
            chartProcessData.chartChanged(tab);
        },
        // 切换曲线
        switchChart(i) {
            this.select_item_indx = -1;
            this.popvisible1 = false;
            this.$global_var.ui_config.chart_tab = i;
            this.change_page(this.$global_var.ui_config.chart_tab);
        },
        jobChartData() {
            // console.log("jbChartData",this.$store.state.g_status.jobChartItems.length)
            return typeof this.$store.state.g_status.jobChartItems === 'undefined' || this.$store.state.g_status.jobChartItems.length === 0 ? this.jobChartItems : this.$store.state.g_status.jobChartItems
        }
    }
}
</script>

