<template>
    <div class="err-box" v-if="isShow">
        <div class="white-box" @click="clearDeviceErr">
            <svg-loader class="alarm-icon" icon="ActiveRedAlarm" size='4.5rem' color="red"></svg-loader>
            <div class="alarm-text">
                {{ $t('alarm.errRest') }}
            </div>
        </div>
        <div class="specific-alarm-msg">
            <span class="alarm-err-text">{{ $t('alarm.errCode') }}</span>
            <span class="alarm-code">{{ errCode }}</span><span class="err-reason-text">{{
                $t('alarm.errReason')
            }}</span>
            <span class="err-reason">
                {{ headText }}
            </span>
            <div class="more-err-reason">
                {{ endText }}
            </div>
        </div>
    </div>
</template>
<script>
import { pdataAlarmNoteApi } from '@/api/alarm/alarm'
import { motionErrClearApi } from '@/api/device/device'
import { messageApi } from '../../message/message'

export default {
    data() {
        return {
            isShow: true,
            desc: "未知原因",
            isHasGetMsg: false,
            module: "ALARM"
        }
    },
    props: {
        state: {
            type: String,
            default: "nok"
        },
        code: {
            type: String,
            default: "4334",
        },
    },
    methods: {
        cancel() {
            this.isShow = false
        },
        initData() {

        },
        clearDeviceErr() {
            if (this.module in this.$store.state.f_status.permissions && this.$store.state.f_status.permissions[this.module] == 4) {
                motionErrClearApi().then((result) => {
                    if (result.status == 0) {
                        let msg = this.$t('alarm.clsSuc')
                        messageApi.success([msg])
                    } else {
                        messageApi.error(["清错失败"])
                    }
                });
            } else {
                messageApi.warning(['清除告警失败，权限被拒绝!!!'])
            }

        },
        getErrorMsg() {
            pdataAlarmNoteApi(this.errCode).then((result) => {
                if (result.data && result.data.alarm_desc) {
                    this.desc = result.data.alarm_desc
                }
                this.isHasGetMsg = true
            }).catch(() => {
                this.isHasGetMsg = true

            });
        }
    },
    computed: {
        headText() {
            let str = this.desc
            return str.substring(0, 11)
        },
        endText() {
            let str = this.desc
            return str.substring(11, this.desc.length)
        },
        errCode() {
            let num = this.$store.state.g_status.alarm_code
            let str = `${parseInt(num).toString(16)}`
            while (str.length < 4) {
                str = `0${str}`
            }
            return str
        },
        alarmHisLevel() {
            // return this.$store.state.f_status.userPrmsnLvl['ALARM']
            return 4
        }
    },
    watch: {
        errCode() {
            if (!this.isHasGetMsg) {
                this.getErrorMsg()
            }
        }
    },
    created() {
        this.getErrorMsg()
    }
}
</script>
<style scoped>
.err-box {
    position: relative;
    width: 95vw;
    height: 15.625vh;
    border-radius: 0.375rem;
    background: var(--CE61F00);
    color: var(--fff);

}

.white-box {
    width: 18.75rem;
    height: 6.25rem;
    background-color: var(--fff);
    border-radius: 6px;
    box-sizing: border-box;
    border: 1px solid #FFFFFF;
    box-shadow: 0px 10px 10px 2px rgba(0, 0, 0, 0.3);

    position: absolute;
    top: 0.6875rem;
    left: 50%;
    transform: translateX(-50%);
}

.alarm-icon {
    position: absolute;
    left: 1rem;
    top: 50%;
    transform: translateY(-50%);
}

.alarm-text {
    font-weight: 600;
    letter-spacing: 0px;
    font-size: 2.8125rem;
    color: var(--CE61F00);
    height: 6.25rem;
    line-height: 5.25rem;
    position: relative;
    top: 7%;
    left: 2.6rem;
}

.specific-alarm-msg {
    position: absolute;
    top: 7.625rem;
    left: 5rem;
    width: 100%;
    height: 5.5rem;
}

.alarm-err-text,
.alarm-code,
.err-reason-text,
.err-reason {
    float: left;
    position: relative;
    left: 5.3125rem;
    font-size: var(--font1_5);
    font-weight: bold;
    line-height: 2.2125rem;
    letter-spacing: 0px;
    color: #FFFFFF;
}

.alarm-code {
    left: 5.6125rem;
}

.err-reason-text {
    left: 8.6125rem;
}

.err-reason {
    display: inline-block;
    left: 9rem;
    right: 5.3125rem;

}

.more-err-reason {
    position: absolute;
    left: 5.3125rem;
    font-family: var(--R-B);
    font-size: var(--font1_5);
    font-weight: bold;
    line-height: 2.2125rem;
    letter-spacing: 0px;
    color: #FFFFFF;
    top: 2.5rem;
    overflow: hidden;
}

.white-box:active {
    cursor: pointer;
    background-color: var(--CDE);
}
</style>