<template>
    <div :class="{ 'right-box': true, 'nok': isok == false }">
        <div v-if="isok" class="right-msg">{{ isOkText }}</div>
        <div v-if='isok == false' class="err-line">
            <svg-loader class="alarm-icon" icon="StaticWhiteAlarm" size="3rem"></svg-loader>
            <div class="err-state">{{ $t('monitor.qualified_nok') }}</div>
        </div>
        <div v-if='isok == false' class="err-msg">
            <span class="err-code-text">{{ $t('monitor.unqualifiedcode') }}：{{ errcode }}</span>
            <span class="err-desc-text">{{ $t('monitor.reason') }}：{{ msg }}</span>
        </div>
    </div>
</template>
<script>
import { pdataResultDescApi } from '@/api/alarm/alarm'
export default {
    components: {},
    data() {
        return {
            isShow: true,
            msg: '--',
            errcode: "",
        }
    },
    props: {
        desc: {
            type: String,
            default: "--",
        },
    },
    methods: {
        cancel() {
            this.isShow = false
        },
        getErrorMessage() {
            // this.msg = '...';
            if (this.$store.state.g_status.fail_code) {
                this.errcode = this.$store.state.g_status.fail_code.toString(16).toUpperCase().padStart(4, '0');
                if (this.code == "0000") {
                    return;
                }
                pdataResultDescApi(this.errcode).then((result) => {
                    if (result.status == 0 && result.data) {
                        this.msg = result.data
                    }
                });
            }
        },

    },
    computed: {
        isOkText() {
            return this.isok ? this.$t('monitor.qualified_ok') : this.$t('monitor.qualified_nok')
        },
        isok() {
            return this.$store.state.g_status.fail_code == 0 && this.$store.state.g_status.isok;
        }

    },
    created() {
        this.getErrorMessage()
    },
    watch: {
        isok: {
            handler(newval) {
                if (newval == false)
                    this.getErrorMessage();
            },
            immediate: true
        },

        "$store.state.g_status.fail_code": {
            handler() {
                this.getErrorMessage();
            },
            immediate: true
        }
    }
}
</script>
<style scoped>
.right-box {
    position: relative;
    width: 95vw;
    height: 15.625vh;
    border-radius: 0.375rem;
    background: var(--C00c155);
    color: var(--fff);

}

.err-code-text {
    display: inline-block;
    font-weight: bold;
    font-size: var(--font1_625);
}

.err-code-val {
    margin-right: 30px;

}

.right-msg {
    position: absolute;
    font-size: 4.5rem;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.nok {
    background-color: var(--CE61F00) !important;
}

.err-line {
    width: 100vw;
    height: auto;
    position: absolute;
    top: 35%;
}

.err-state {
    position: absolute;
    top: -18px;
    font-size: 2.8125rem;
    left: 50%;
    transform: translate(-50%, -50%);
    /* text-align: center; */
    font-weight: 900;

}

.err-msg {
    position: relative;
    top: 7.8672vh;
    left: 50%;
    width: 600px;
    transform: translateX(-50%);
    font-weight: bold;
    font-size: var(--font1_625);
}



.alarm-icon {
    position: absolute;
    top: -18px;
    transform: translateY(-50%);
    left: 32%;
    color: white;
}

.err-desc-val {
    width: 31.25rem;
    overflow: hidden;
    text-overflow: ellipsis;
}

.err-desc-text {
    text-align: center;
    margin-left: 5rem;
    font-weight: bold;
    /* font-size: var(--font1_625); */
}
</style>